import React from 'react';
import Header from 'components/Header';
import Navigation from 'components/Navigation';
import routes from 'routes';
import './styles.css';

const App = () => (
  <div className="App">
      <Header text="movie mate">
        <Navigation />
      </Header>
      {routes}
  </div>
);

export default App;



