import React from 'react';
import './styles.css';
import icons from './assets/icon-sprite.svg';

const iconList = [
  `${icons}#icon-html`,
  `${icons}#icon-css`,
  `${icons}#icon-javascript`,
  `${icons}#icon-webpack`,
  `${icons}#icon-react`,
  `${icons}#icon-react-router`,
  `${icons}#icon-redux`
];

const StackList = () => (
  <ul className="StackList">
    {iconList.map((icon, idx) => (
      <li key={idx} className="StackList__item">
        <svg className="StackList__icon">
          <use href={icon} />
        </svg>
      </li>
    ))}
  </ul>
);

export default StackList;



