import React from 'react';
import './styles.css';

const MemberCard = ({ members, match }) => {
  const getMemberInfo = id => members.find(member => member.id === id);

  const member = getMemberInfo(match.params.memberId);

  return (
    <div className="MemberCard">
      <img className="MemberCard__img" src={member.img} alt={member.name} />
      <div className="MemberCard__info">
        <h2 className="MemberCard__name">{member.name}</h2>
        <p className="MemberCard__pos">{member.position}</p>
      </div>
    </div>
  );
};

export default MemberCard;



