import React from 'react';
import jobs from './jobs';
import './styles.css';

const CareerTable = () => (
  <table className="CareerInfo">
    <caption>Currently we are looking for</caption>
    <tbody>
      {jobs.map(job => (
        <tr key={job.position}>
          <td>{job.position}</td>
          <td>
            <ul>
              {job.skills.map((skill, idx) => <li key={idx}>- {skill}</li>)}
            </ul>
          </td>
        </tr>
      ))}
    </tbody>
  </table>
);

export default CareerTable;



