import React from 'react';
import WatchlistCard from 'components/WatchlistCard';
import EmptyListMsg from './EmptyListMsg';
import './styles.css';

export default class Watchlist extends React.Component {
  render() {
    const { movies, onDetachFromWatchlist } = this.props;
    return (
      <div className="Watchlist">
        <h2 className="Watchlist__title">Watchlist</h2>
        {movies.length === 0 ? <EmptyListMsg /> : ''}
        {movies.map(movie => (
          <WatchlistCard
            key={movie.id}
            img={movie.poster_path}
            title={movie.title}
            date={movie.release_date.slice(0, 4)}
            rating={movie.vote_average}
            onDetachFromWatchlist={() => {
              onDetachFromWatchlist(movie.id);
            }}
          />
        ))}
      </div>
    );
  }
}



