import React from 'react';
import { NavLink } from 'react-router-dom';
import links from './links';
import './styles.css';

const AboutNav = ({ path }) => (
  <nav className="AboutNav">
    {links.map(link => (
      <NavLink
        key={link.text}
        to={`${path}${link.url}`}
        className="AboutNav__link"
        activeClassName="AboutNav__link--active"
      >
        {link.text}
      </NavLink>
    ))}
  </nav>
);

export default AboutNav;



