import React from 'react';
import MovieInfo from './MovieInfo';
import './styles.css';

const MovieCard = ({ movie, onDeleteMovie, onAttachToWatchlist }) => (
  <div className="MovieCard">
    <span className="MovieCard__rating">{movie.vote_average}</span>
    <img
      className="MovieCard__poster"
      src={`https://image.tmdb.org/t/p/w300${movie.poster_path}`}
      alt=""
    />
    <MovieInfo
      title={movie.title}
      overview={movie.overview.slice(0, 100)}
      release={movie.release_date.slice(0, 4)}
    />
    <button className="MovieCard__btn" onClick={onAttachToWatchlist}>
      +
    </button>
  </div>
);

export default MovieCard;



