import React, { Component } from 'react';
import * as api from 'api';
import { saveState, loadState } from 'localStorage';
import MovieList from 'components/MovieList';
import SearchForm from 'components/SearchForm';
import SearchCategories from 'components/SearchCategories';
import WatchList from 'components/Watchlist';
import './styles.css';

class MoviesPage extends Component {
  constructor() {
    super();

    this.state = {
      movies: [],
      watchlist: []
    };

    this.handleSearchFormSubmit = this.handleSearchFormSubmit.bind(this);
    this.handleSearchByCategory = this.handleSearchByCategory.bind(this);
  }

  componentWillMount() {
    this.setState({
      watchlist: loadState() || []
    });
  }

  async componentDidMount() {
    const movies = await api.getMoviesByCategory(api.types.POPULAR);

    this.setState({
      movies: movies
    });
  }

  handleDeleteMovie = id => {
    this.setState({
      movies: this.state.movies.filter(movie => movie.id !== id)
    });
  };

  handleAttachToWatchList = id => {
    const isInWatchlist = this.state.watchlist.find(movie => movie.id === id);

    if (!isInWatchlist) {
      this.setState(
        {
          watchlist: [
            ...this.state.watchlist,
            this.state.movies.find(movie => movie.id === id)
          ]
        },
        () => saveState(this.state.watchlist)
      );
    }
  };

  handleDetachFromWatchlist = id => {
    this.setState(
      {
        watchlist: this.state.watchlist.filter(movie => movie.id !== id)
      },
      () => saveState(this.state.watchlist)
    );
  };

  async handleSearchFormSubmit(query) {
    const movies = await api.getMoviesByName(query);

    this.setState({
      movies: movies
    });
  }

  async handleSearchByCategory(category) {
    const movies = await api.getMoviesByCategory(category);

    this.setState({
      movies: movies
    });
  }

  render() {
    const { movies, watchlist } = this.state;

    return (
      <div className="MoviesPage">
        <div className="MoviesPage__sidebar">
          <SearchForm onSubmit={this.handleSearchFormSubmit} />
          <SearchCategories onSearch={this.handleSearchByCategory} />
          <WatchList
            movies={watchlist}
            onDetachFromWatchlist={this.handleDetachFromWatchlist}
          />
        </div>
        <MovieList
          movies={movies}
          onDeleteMovie={this.handleDeleteMovie}
          onAttachToWatchlist={this.handleAttachToWatchList}
        />
      </div>
    );
  }
}

export default MoviesPage;



