import React from 'react';
import classNames from 'classnames';
import Button from './Button';
import { types } from 'api';
import './styles.css';

export default class SearchCategories extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activeLink: types.POPULAR
    };
  }

  onChangeActiveLink = type => {
    this.setState({
      activeLink: type
    });
  };

  render() {
    const { onSearch } = this.props;

    const getClassName = type =>
      classNames({
        SearchCategories__btn: true,
        'SearchCategories__btn--active': this.state.activeLink === type
      });

    return (
      <div className="SearchCategories">
        {Object.keys(types).map(key => (
          <Button
            key={key}
            cls={getClassName(types[key])}
            text={types[key]}
            onClick={() => {
              onSearch(types[key]);
              this.onChangeActiveLink(types[key]);
            }}
          />
        ))}
      </div>
    );
  }
}



