import React from 'react';
import { Link } from 'react-router-dom';
import './styles.css';

const Header = ({ text, children }) => (
  <header className="Header">
    <h1 className="Header__logo">
      <Link to="/">{text}</Link>
    </h1>
    {children}
  </header>
);

export default Header;



