import React from 'react';
import { NavLink, Route } from 'react-router-dom';
import MemberCard from 'components/MemberCard';
import members from './members';
import './styles.css';

const TeamList = ({ match }) => (
  <div className="TeamList">
    <ul className="TeamList__members">
      {members.map(member => (
        <li key={member.id}>
          <NavLink
            className="TeamList__members__link"
            activeClassName="TeamList__members__link--active"
            to={`${match.path}/${member.id}`}
          >
            {member.name}
          </NavLink>
        </li>
      ))}
    </ul>
    <div className="TeamList__card">
      <Route
        path={`${match.path}/:memberId`}
        render={props => <MemberCard {...props} members={members} />}
      />
    </div>
  </div>
);

export default TeamList;



