import React from 'react';
import { Switch, Route } from 'react-router-dom';
import AboutNav from 'components/AboutNav';
import TeamList from 'components/TeamList';
import StackList from 'components/StackList';
import CareerTable from 'components/CareerTable';
import './styles.css';

const AboutPage = ({ match }) => (
  <div className="AboutPage">
    <div className="AboutPage__nav">
      <AboutNav path={match.path} />
    </div>
    <div className="AboutPage__body">
      <Switch>
        <Route path={`${match.path}/team`} component={TeamList} />
        <Route path={`${match.path}/stack`} component={StackList} />
        <Route path={`${match.path}/career`} component={CareerTable} />
      </Switch>
    </div>
  </div>
);

export default AboutPage;



