import React from 'react';
import MovieCard from 'components/MovieCard';
import NotFoundMsg from './NotFoundMsg';
import './styles.css';

const MovieList = ({ movies, onDeleteMovie, onAttachToWatchlist }) => (
  <div className="MovieList">
    {movies.length === 0 ? <NotFoundMsg /> : ''}
    {movies.map(movie => (
      <div className="MovieList__item" key={movie.id}>
        <MovieCard
          movie={movie}
          onDeleteMovie={() => {
            onDeleteMovie(movie.id);
          }}
          onAttachToWatchlist={() => {
            onAttachToWatchlist(movie.id);
          }}
        />
      </div>
    ))}
  </div>
);

export default MovieList;



