import React from 'react';
import { NavLink } from 'react-router-dom';
import links from './links';
import './styles.css';

const Navigation = () => (
  <nav className="Navigation">
    {links.map(link => (
      <NavLink
        key={link.text}
        to={link.to}
        exact={link.to !== '/about'}
        className="Navigation__link"
        activeClassName="Navigation__link--active"
      >
        {link.text}
      </NavLink>
    ))}
  </nav>
);

export default Navigation;



